@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top-menu-lead {
  color: #1890ff;
  display: inline-block;
  font-size: 18px;
  font-weight: bold;
}

.top-menu {
  width: 100%;
  background-color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px 20px;
  border-bottom: 1px solid #1890ff;
}
.top-menu > * {
  width: 33%;
}
.right {
  text-align: right;
}
.right > a {
  padding: 0 10px;
}

.top-menu *:hover {
  cursor: pointer;
}

.top-menu-icon {
  display: none;
  font-size: 1.2rem;
}

@media screen and (max-width: 600px) {
  .top-menu {
    padding: 5px 5px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .top-menu-icon {
    display: inline-block;
    text-align: right;
    padding-right: 30px;
  }

  .top-menu > .left,
  .top-menu > .right {
    display: none;
  }
  .top-menu-lead,
  .top-menu-icon {
    width: 50%;
  }

  .top-menu.toggled {
    display: block;
  }

  .top-menu.toggled > * {
    width: 100%;
    padding: 10px 5px;
  }
  .top-menu.toggled > .top-menu-icon {
    position: absolute;
    top: 5px;
    right: -240px;
  }

  .top-menu.toggled *:not(.top-menu-lead):not(.top-menu-icon):not(input) {
    display: block !important;
    text-align: left;
    margin: 0;
  }
}

.root {
  font-family: "Roboto", sans-serif;
  margin: 0;
}
/* Home */
.cover {
  width: 100%;
  height: 400px;
  background-image: url("/cdmx.jpg");
  padding: 100px 0;
  background-size: cover;
  background-position: center;
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
  font-family: "Avenir";
}
.center {
  text-align: center;
}
.cover h2,
.cover p {
  color: #fff;
}
.job-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.job-card {
  width: 33%;
  box-sizing: border-box;
  padding: 10px;
}
.job-card img {
  width: 100%;
}
.container {
  width: 80%;
  padding: 20px 0;
  margin: 0 auto;
}
.shortlink-container {
  margin: 5px;
}
.shortlinks {
  padding: 0 5px;
}
/* Form */
.form-container {
  background-color: #fff;
  border: 1px solid #1890ff;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 10px 0;
}
.form-container img {
  width: 40%;
}
.form-container input {
  margin: 0 10px;
}
button,
.btn {
  padding: 10px 20px;
  border-radius: 5px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  margin: 10px;
}
.btn:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}
.btn-positive {
  background-color: #65b5ff;
  border: 1px solid #1890ff;
  font-weight: bold;
}
.btn-positive:hover {
  background-color: #1890ff;
}
.btn-negative {
  background-color: #fff;
  border: 1px solid #666;
}
.btn-negative:hover {
  background-color: #ccc;
}
.form-progress {
  padding: 20px 0;
}
.loading {
  padding: 10px;
  font-size: 1rem;
  background-color: #1890ff;
  color: #fff;
  border-radius: 5px;
}
input[type="text"],
input[type="password"],
input[type="email"],
input[type="select"] {
  margin: 5px 10px;
  padding: 5px 10px;
  border: 1px solid #1890ff;
  border-radius: 5px;
}

/* Mapbox */
.mapboxgl-popup {
  max-width: 200px;
}

.mapboxgl-popup-content {
  text-align: center;
  font-family: "Avenir";
}

.mapboxgl-popup-close-button {
  display: none;
}

/* Media queries */
@media screen and (max-width: 600px) {
  .ant-steps-item-description {
    display: none;
  }
  .form-container img {
    width: 100%;
  }
  .job-card {
    width: 50%;
  }
}

